@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap);
html,body{background-image:radial-gradient(circle farthest-corner at 12.3% 19.3%, #5558da 0%, #5fd1f9 100.2%);font-family:"Bitter", serif;color:#35312c;width:100%;height:100%;margin:0px;border:0;overflow:hidden;display:block}#quote-box{background-color:rgba(253,255,255,0.7);position:absolute;min-width:65%;max-width:100%;top:50%;left:50%;transform:translate(-50%, -50%);border-radius:45px 15px 45px 15px;-moz-border-radius:45px 15px 45px 15px;-webkit-border-radius:45px 15px 45px 15px;border:0px solid #000000;box-shadow:5px 5px 32px 0px rgba(0,0,0,0.08)}@media (orientation: portrait){#quote-box{min-width:85%}}#quote-box #text h1{margin:auto;padding:25px 25px 10px 25px;font-size:2.5vw;text-align:end}#quote-box #author h2{font-size:2vw;text-align:center}#quote-box hr{border:0;height:0;border-top:1px solid rgba(0,0,0,0.1);border-bottom:1px solid rgba(255,255,255,0.3)}#quote-box .buttons{display:flex;justify-content:space-around;text-align:justify;align-items:center;line-height:0.8em;color:rgba(53,49,44,0.9);margin:8px 0px}#quote-box .buttons #tweet-quote{display:inline-block;color:inherit;transition:ease 0.3s;font-size:6vw}#quote-box .buttons #tweet-quote:hover{cursor:pointer;color:#239bca;transform:scale(1.1, 1.1)}#quote-box .buttons #new-quote{height:100%;transition:ease 0.3s;font-size:10vw;transform:rotate(90deg)}#quote-box .buttons #new-quote:hover{cursor:pointer;color:#35312c;transform:rotate(90deg) scale(1.1, 1.1)}

